import { Card, CardBody } from '@chakra-ui/react'
import { type EffectState } from 'patronum/status'

import { type ClientAnalytic } from '~src/shared/api'
import { DescriptionText, ErrorMessage, Spin } from '~src/shared/ui'

interface ClientAnalyticCardProps extends Partial<ClientAnalytic> {
	load?: { loading: boolean; loader?: React.ReactNode }
	onClick?: (args: { event: any; clientId: number; plotCultId: number | undefined; plotId: number }) => void
}

export function ClientAnalyticCard(props: ClientAnalyticCardProps) {
	const {
		year,
		productName,
		culture,
		region,
		percentSubs,
		percentVolume,
		sumSubsClient,
		sumVolumeClient,
		foreignMarkSumSubcides,
		foreignMarkSumVolume,
		load,
		onClick,
		...otherProps
	} = props

	return (
		<Card {...otherProps}>
			<CardBody>
				{year && <DescriptionText title=':'>{year}</DescriptionText>}
				{productName && <DescriptionText title=':'>{productName}</DescriptionText>}
				{culture && <DescriptionText title=':'>{culture}</DescriptionText>}
				{region && <DescriptionText title=':'>{region}</DescriptionText>}
				{percentSubs && <DescriptionText title=' :'>{percentSubs}</DescriptionText>}
				{percentVolume && <DescriptionText title=' :'>{percentVolume}</DescriptionText>}
				{sumSubsClient && <DescriptionText title='  :'>{sumSubsClient}</DescriptionText>}
				{sumVolumeClient && <DescriptionText title='  :'>{sumVolumeClient}</DescriptionText>}
				{foreignMarkSumSubcides && (
					<DescriptionText title='   :'>{foreignMarkSumSubcides}</DescriptionText>
				)}
				{foreignMarkSumVolume && (
					<DescriptionText title='   :'>{foreignMarkSumVolume}</DescriptionText>
				)}
			</CardBody>

			{load && load.loading && <>{load.loader ? <>{load.loader}</> : <Spin />}</>}
		</Card>
	)
}
